import { Session } from 'src/app/models/session';
import { SessionsManagerService } from 'src/app/services/sessions-manager-service';
import { EnvService } from "src/app/services/env.service";
import { DatePickerComponent } from "./../../common/date-picker/date-picker.component";
import { Component, OnInit,ViewChild, ElementRef } from "@angular/core";
import { FormGroup, FormControl } from "@angular/forms";
import { from } from "rxjs";
import { concatMap } from "rxjs/operators";
import { ModalController } from "@ionic/angular";
import {Chart} from "chart.js";
import moment from 'jalali-moment';

@Component({
  selector: "app-pvd-income-report",
  templateUrl: "./pvd-income-report.page.html",
  styleUrls: ["./pvd-income-report.page.scss"],
})
export class PvdIncomeReportPage implements OnInit {
  toDate: Date;
  fromDate: Date;
  @ViewChild("lineCanvas", {static:false, read:ElementRef}) lineCanvs: ElementRef;
  private lineChart: Chart;


  constructor(
    private modalController: ModalController,
    private env: EnvService,
    private sessionsManager:SessionsManagerService
  ) {}

  ngOnInit() {
    this.toDate = new Date();
    this.fromDate = new Date();
    this.fromDate.setTime(new Date().getTime() - 10 * 24 * 60 * 60 * 1000);
    
  }
  ionViewWillEnter() {
    this.requestReport();
  }
  dateTo() {
    this.presentDatePickerModal(1).subscribe();
  }
  dateFrom() {
    this.presentDatePickerModal(0).subscribe();
  }
  presentDatePickerModal(index) {
    let dateToSend;
    if (index == 1) {
      dateToSend = this.toDate;
    } else {
      dateToSend = this.fromDate;
    }
    return from(
      this.modalController.create({
        component: DatePickerComponent,
        componentProps: {
          date: dateToSend,
        },
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {
          if (data["data"]) {
            if (index == 1) {
              this.toDate = data["data"]["date"];
            } else {
              this.fromDate = data["data"]["date"];
            }
            this.requestReport();
          }
        });
        return from(modal.present());
      })
    );
  }
  getDateFromText() {
    return this.env.dateStringToJalaliDate(
      this.env.generateUTCString(this.fromDate)
    );
  }
  getDateToText() {
    return this.env.dateStringToJalaliDate(
      this.env.generateUTCString(this.toDate)
    );
  }
  requestReport()
  {
    this.sessionsManager.incomeReport(this.fromDate, this.toDate).subscribe(result=>{
      this.drawChart(result);
    });
  }
  drawChart(sessions:Session[])
  {
    
    let labels = [];
    let data = [];
    const daysDiff = Math.floor(Math.abs(<any>this.toDate - <any>this.fromDate) / (1000*60*60*24));
    for(let i =0; i< daysDiff; i++)
    {
      let date = new Date();
      
      date.setDate(this.fromDate.getDate()+i);
      let text = this.env.dateStringToJalaliDate(this.env.generateUTCString(date), 'jMM/jDD');
      let income = 0;
      sessions.filter(session=>{
        let sessionDate = new Date(session.started);
        if(sessionDate.getDate() == date.getDate()
          && sessionDate.getMonth() == date.getMonth())
          {
            return true;
          }
          return false;
      }).forEach(session=>{
        income+= this.sessionsManager.calculateSessionTotalFee(session);
      });
      data.push(income);
      labels.push(text);
    }
    this.lineChart = new Chart(this.lineCanvs.nativeElement, {
      type: "line",
      data: {
        labels: labels,
        datasets: [
          {
            label: "",
            data: data,
            
          }
        ]
      },
      options: {
        legend:{
          labels:{
            fontFamily:"myIranSansFaNum"
          }
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                fontFamily:"myIranSansFaNum",
                fontSize:10
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                fontFamily:"myIranSansFaNum",
                fontSize:10
              }
            }
          ]
        }
      }
    });
  }
}
